<template>
  <div id="app">
    <header>
      <h1>Randomfy</h1>
      <h4>Your new music playlist.</h4>
    </header>
    <main>
      <section class="player">
        <h2 class="song-title">{{ current.title }} - <span>{{ current.artist }}</span></h2>
        <div class="control">
          <button class="prev" @click="prev"><i class="fa fa-step-backward" aria-hidden="true"></i></button>
          <button class="play" type="checkbox" v-if="!isPlaying" @click="play"><i class="fa fa-play" aria-hidden="true"></i></button>
          <button class="pause" v-else @click="pause"><i class="fa fa-pause" aria-hidden="true"></i></button>
          <button class="next" @click="next"><i class="fa fa-step-forward" aria-hidden="true"></i></button>
        </div>
      </section>
      <section class="playlist">
        <h3>Playlist</h3>
        <button v-for="song in songs" :key="song.src" @click="play(song)" 
        :class="(song.src == current.src) ? 'song playing' : 'song'">
          {{ song.title }} - {{ song.artist }}
        </button>
      </section>
    </main>
  </div>
</template>

<script>

export default {
  name: 'app',
  data () {
    return {
      current: {},
      index: 0,
      isPlaying: false,
      songs: [
        {
          title: 'Take On Me',
          artist: 'A-ha',
          src: require('./assets/aha-takeonme.mp3'),
        },
        {
          title: 'Wait',
          artist: 'M83',
          src: require('./assets/m83-wait.mp3')
        },
        {
          title: 'Porcelain',
          artist: 'Red Hot Chilli Peppers',
          src: require('./assets/redhotchilipeppers-porcelain.mp3')
        },
        {
          title: 'Easy',
          artist: 'CRO',
          src: require('./assets/cro-easy.mp3')
        }
      ],
      player: new Audio()
    }
  },
  methods: {
    play (song) {
      if (typeof song.src != "undefined") {
        this.current = song;
        this.player.src = this.current.src;
      }

      this.player.play();
      this.player.addEventListener('ended', function () {
        this.index++;
        if (this.index > this.songs.length - 1) {
        this.index = 0;
      }

      this.current = this.songs [this.index];
      this.play(this.current);

      }.bind(this));
      this.isPlaying = true;
    },

    pause () {
      this.player.pause();
      this.isPlaying = false;
    },

    next() {
      this.index++;
      if (this.index > this.songs.length - 1) {
        this.index = 0;
      }

      this.current = this.songs [this.index];
      this.play(this.current);
    },

    prev() {
      this.index--;
      if (this.index < 0) {
        this.index = this.songs.length - 1;
      }

      this.current = this.songs [this.index];
      this.play(this.current);
    }

  },

  created () {
    this.current = this.songs[this.index];
    this.player.src = this.current.src;
    this.player.play();
  }
}
</script>

